import React from 'react';
import { detect } from 'detect-browser'; // eslint-disable-line import/no-extraneous-dependencies
import Modernizr from 'modernizr';

class Support extends React.Component {
    constructor(props) {
        super(props);
        this.state = { hasError: false };
    }

    componentDidCatch(error, info) {
        // Display fallback UI
        this.setState({ hasError: true });
        // You can also log the error to an error reporting service
        console.error(error, info);
    }

    render() {
        console.log('history', Modernizr.history);
        console.log('localstorage', Modernizr.localstorage);
        console.log('es5object', Modernizr.es5object);
        console.log('es6array', Modernizr.es6array);
        console.log('es6object', Modernizr.es6object);
        console.log('cssborderimage', Modernizr.borderimage);
        console.log('cssflexbox', Modernizr.flexbox);
        console.log('csstransforms', Modernizr.csstransforms);
        console.log('csstransforms3d', Modernizr.csstransforms3d);
        console.log('preserve3d', Modernizr.preserve3d);


        const browser = detect();
        if (browser) {
            const browser_version = browser.version.replace(/\./g, '');
            const browser_name = browser.name;
            let isSupported = true;

            console.log(browser_name);
            console.log(browser_version);
            console.log(browser.os);

            if (
                (browser_name === 'ie' && browser_version < 1100) ||
                (browser_name === 'firefox' && browser_version < 4100) ||
                    (browser_name === 'firefox' && browser_version < 2800 && browser.os === 'Mac OS') ||
                (browser_name === 'chrome' && browser_version < 3601985) ||
                (browser_name === 'opera' && browser_version < 2401558) ||
                (browser_name === 'safari' && browser_version < 911) ||
                (browser_name === 'edge'  && browser_version < 15150630)
            ) {
                isSupported = false;
            }

            console.log(isSupported ? 'Supported' : 'Not Supported');
        }


        if (this.state.hasError) {
            // You can render any custom fallback UI
            return <h1> Not Supported. </h1>;
        }
        return this.props.children;
    }
}


export default Support;
